export default {
	data() {
		return {
			basicsList: [{
				icon: 'usefullfill',
				name: '开始'
			}, {
				icon: 'radioboxfill',
				name: '等待'
			}, {
				icon: 'roundclosefill',
				name: '错误'
			}, {
				icon: 'roundcheckfill',
				name: '完成'
			}, ],
			basics: 0,
			numList: [{
				name: '开始'
			}, {
				name: '等待'
			}, {
				name: '错误'
			}, {
				name: '完成'
			}, ],
			num: 0,
			scroll: 0
		}
	},
	methods: {
		basicsSteps() {
			let that = this
			that.basics = that.basics == that.basicsList.length - 1 ? 0 : that.basics + 1
		},
		numSteps() {
			let that = this
			that.num = that.num == that.numList.length - 1 ? 0 : that.num + 1
		},
		scrollSteps() {
			let that = this
			that.scroll = that.scroll == 9 ? 0 : that.scroll + 1
		}
	}
}
